@import url('https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400..700;1,400..700&family=Neuton:ital,wght@0,200;0,300;0,400;0,700;0,800;1,400&family=Pathway+Gothic+One&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', sans-serif;
}

html, body {
  height: auto;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f5f5dc, #e0d6f5);
  background-attachment: fixed;
  color: #3b2f2f;
  font-family: 'Arimo', Arial, sans-serif;
  cursor: url('data:image/svg+xml;utf8,<svg fill="none" stroke="%233b2f2f" stroke-width="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M2 2l20 10-20 10V2z"/></svg>') 4 4, auto;
  background-image: 
    linear-gradient(to right, rgba(60, 40, 120, 0.25) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(60, 40, 120, 0.25) 1px, transparent 1px);
  background-size: 32px 32px;
  overflow-x: hidden;
  overflow-y: auto;
}

body {
  line-height: 2;
  color: #000;
}

.binary-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
  pointer-events: none;
}
#binary-canvas {
  width: 100vw;
  height: 100vh;
  display: block;
  opacity: 1;
  pointer-events: none;
}
.page-container {
  width: 90%;
  max-width: 1300px;
  margin: auto;
  padding: 20px;
  position: relative;
  z-index: 1;
  background: rgba(158, 152, 152, 0.04); 
  box-shadow: 0 2px 16px rgba(0,0,0,0.06);
  border-radius: 12px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  border-bottom: 1px solid #eee;
}

.logo {
  font-weight: bold;
  font-size: 35px;
  text-align: center;
}

.hero {
  text-align: center;
  margin: 50px 0 30px;
}

.profile-pic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
}

.cta-button {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px 24px;
  background-color: #000;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
  text-decoration: none;
  display: inline-block;
}

.cta-button:hover {
  background-color: #444;
}

.intro {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 50px 0;
  gap: 20px;
}

.intro .text {
  flex: 1 1 50%;
}

.intro .image img {
  width: 100%;
  max-width: 400px;
  border-radius: 8px;
}

.testimonial {
  background-color: #f9f9f9;
  padding: 40px 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  height: 160px;
  text-align: center;
  font-style: italic;
  border-left: 4px solid #ccc;
  border-right: 4px solid #ccc;
}

.testimonial cite {
  display: block;
  margin-top: 15px;
  font-style: normal;
  font-weight: bold;
}

.portfolio {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 20px;
  margin: 50px 0;
}

.portfolio-item {
  text-align: center;
  transition: transform 0.3s ease;
}

.portfolio-item:hover {
  transform: scale(1.03);
}

.portfolio-item img {
  width: 100%;
  max-width: 100%;
  height: 220px;
  object-fit: cover;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.portfolio-item p {
  margin-top: 10px;
  font-weight: bold;
}

.contact {
  margin: 50px 0;
}

.contact h2 {
  margin-bottom: 20px;
}

.contact form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.contact input,
.contact textarea {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.contact button {
  width: 120px;
  padding: 10px;
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.contact button:hover {
  background-color: #444;
}

.footer {
  text-align: center;
  padding: 25px 0;
  font-size: 14px;
  color: #777;
  border-top: 1px solid #eee;.skills-section {
  padding: 40px 20px;
  text-align: center;
}
.skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns for desktop */
  gap: 2rem;
  margin-top: 2rem;
}

.skill-card {
  background: #231942; /* deep purple, adjust to match your cards */
  border-radius: 16px;
  padding: 2rem 1.5rem;
  color: #fff;
  box-shadow: 0 4px 16px rgba(0,0,0,0.07);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: transform 0.15s, box-shadow 0.15s;
  cursor: pointer;
}

.skill-card:hover {
  transform: translateY(-6px) scale(1.03);
  box-shadow: 0 8px 32px rgba(35,25,66,0.15);
}

.skill-card h3 {
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
  font-weight: 700;
}

.skill-card p {
  font-size: 1rem;
  opacity: 0.85;
}
}